import { useEffect, useState } from 'react';
import { getInfo } from '../helpers/getInfo';

export const useFetch = () => {

  const [stateInfo, setStateInfo] = useState({
    info: [],
    loading: true
  });

  	useEffect(() => {
      getInfo()
        .then( info => {
          setStateInfo({
            info: info,
            loading: false
          });
        }).catch(console.log("mal"))
	}, []);

  return stateInfo;
}